/* src/components/layout/FramerMotionTest/animations/HoverBarButtonAnimations.tsx */

import React from "react";

export const HoverBarButtonAnimations = {
  /* Main Button */
  hoverBarButtonMain: {
    initial: false,
    animate: (isExpanded: boolean) => ({
      width: isExpanded ? "100px" : "70px",
      transform: isExpanded
        ? "translateX(calc(0% + -50px))"
        : "translateX(calc(0% + -15px))",
    }),
    transition: { duration: 0.5, ease: "easeInOut" },
  },

  hoverBarButtonMainInnerLeft: {
    initial: false,
    animate: (isExpanded: boolean) => ({
      /*       justifyContent: isExpanded ? "center" : "center", */
      width: isExpanded ? "40%" : "100%",
    }),
    transition: { duration: 0.5, ease: "easeInOut" },
  },

  hoverBarButtonMainInnerRight: {
    initial: false,
    animate: (isExpanded: boolean) => ({
      opacity: isExpanded ? 1 : 0,
      width: isExpanded ? "60%" : "0%",
      transform: isExpanded ? "translateX(0)" : "translateX(20px)",
    }),
    transition: { duration: 0.5, ease: "easeInOut" },
  },

  /* Menu Button */
  HoverBarButtonMenu: {
    initial: false,
    animate: (isExpanded: boolean) => ({
      width: isExpanded ? "70px" : "70px",
      transform: isExpanded ? "translateX(50px)" : "translateX(70px)",
    }),
    transition: { duration: 0.4, ease: "easeInOut" },
  },

  /* Settings Button */
  HoverBarButtonSettings: {
    initial: {
      width: "70px",
      transform: "translate(125px)",
      opacity: 1,
    },
    animate: (isExpanded: boolean, viewportWidth: number) => ({
      width: isExpanded ? "70px" : "70px",
      transform: isExpanded ? "translateX(125px)" : "translateX(145px)",
      opacity: viewportWidth >= 550 ? 1 : 0,
    }),
    exit: { opacity: 0 },
    transition: { duration: 0.4, ease: "easeInOut" },
  },

  /* Minimize Button */
  HoverBarButtonMinimize: {
    initial: false,
    animate: (isExpanded: boolean) => ({
      width: isExpanded ? "45px" : "45px",
      transform: isExpanded ? "translateX(5px)" : "translateX(50px)",
    }),
    transition: { duration: 0.4, ease: "easeInOut" },
  },
};
